import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RegistrationComponent } from './registration.component';
import { FormSubmittedComponent } from '../form-submitted/form-submitted.component';
import { EditBankdetailsComponent } from '../edit-bankdetails/edit-bankdetails.component';
import { EditContactComponent } from '../edit-contact/edit-contact.component';
import { AuthGuard } from '../auth/auth.guard';

const routes: Routes = [
    
    {path:'',component: RegistrationComponent,canActivate:[AuthGuard]},
    {path:'submitted',component: FormSubmittedComponent,canActivate:[AuthGuard]},

    {path:'edit-contact-details',component: EditContactComponent,canActivate:[AuthGuard]},
    {path:'edit-bank-details',component: EditBankdetailsComponent,canActivate:[AuthGuard]},
 
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RegistrationRouting {}
